<template>
  <v-dialog v-model="dialog" persistent max-width="550">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        class="ma-5"
        outlined
        medium
        v-bind="attrs"
        v-on="on"
        color="primary"
      >
        <v-col cols="12"> <v-icon>mdi-plus</v-icon> add Item </v-col>
      </v-btn>
    </template>
    <v-form ref="form" lazy-validation v-model="valid">
      <v-card>
        <v-card-title>
          <span class="text-h5">Add Item</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <!-- build title field   -->
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                  :rules="titleRules"
                  label=" Title Ar *"
                  required
                  :counter="slider.title.ar"
                  v-model="slider.title.ar"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                  :rules="titleRules"
                  label=" Title En *"
                  required
                  :counter="slider.title.en"
                  v-model="slider.title.en"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-text-field
                  :rules="titleRules"
                  label=" Title Tr *"
                  required
                  :counter="slider.title.tr"
                  v-model="slider.title.tr"
                ></v-text-field>
              </v-col>
              <!--  -->
              <!-- build description field  -->
              <v-col cols="12">
                <v-textarea
                  :rules="descriptionRules"
                  label="Description Ar *"
                  required
                  v-model="slider.description.ar"
                ></v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  :rules="descriptionRules"
                  label="Description En *"
                  required
                  v-model="slider.description.en"
                ></v-textarea>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  :rules="descriptionRules"
                  label="Description Tr *"
                  required
                  v-model="slider.description.tr"
                ></v-textarea>
              </v-col>
              <!--  -->
              <v-col cols="12">
                <v-file-input
                  :rules="imageRules"
                  v-model="file"
                  chips
                  accept="image/png, image/jpeg"
                  label="Image"
                  @change="onFileChange"
              /></v-col>
              <v-col cols="12">
                <v-img
                  contain
                  max-height="250"
                  aspect-ratio="1"
                  v-show="image"
                  :lazy-src="image"
                  :src="image"
                >
                </v-img>
              </v-col>
              <!--  -->
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="rest"> Close </v-btn>
          <v-btn color="secondary" text @click="addSlider"
            ><span v-if="!progressUpdateSlider">Add Item</span>
            <v-progress-circular
              v-if="progressUpdateSlider"
              color="primary"
              indeterminate
            ></v-progress-circular
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </v-dialog>
</template>
<script>
import { sliderController } from "./slider";
export default {
  methods: {
    rest() {
      this.dialog = false;
      this.valid = true;
      this.$refs.form.reset();
      this.testimonial = {};
      this.file = null;
      this.image = null;
    },
    onFileChange(e) {
      if (e != null) {
        const reader = new FileReader();
        reader.readAsDataURL(this.file);
        reader.onload = (e) => {
          this.image = e.target.result;
        };
      } else {
        this.file = null;
        this.image = null;
      }
    },

    addSlider: async function () {
      if (this.$refs.form.validate()) {
        this.progressUpdateSlider = true;
        await sliderController.addSlider(this.file, this.slider);
        this.progressUpdateSlider = false;
        this.dialog = false;
        this.rest();
        this.$emit("getVal");
      }
    },
  },
  data() {
    return {
      // image
      minUrlImage: this.$store.state.imageUlr + "slider/",
      image: null,
      file: null,
      valid: true,
      value: false,
      progressUpdateSlider: false,
      titleRules: [(v) => !!v || "the title field is required"],
      descriptionRules: [(v) => !!v || "description is required"],
      imageRules: [(v) => !!v || "chose image ."],

      slider: {
        title: {},
        description: {},
      },
      dialog: false,
    };
  },
};
</script>